/*==================
1.4. Buttons
====================*/
.btn-custom {
  cursor: pointer;
  display: inline-block;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  position: relative;
  background-color: #45b66f;
  line-height: 24px;
  border: 0;
  color: #fff;
  font-size: 24px;
  font-weight: 400;
  padding: 15px 64px;
  transition: 0.3s;
  /* text-transform: uppercase; */
  border-radius: 10px;
  z-index: 1;
}
.btn-custom::before{
  content: '';
  width: 100%;
  height: 100%;
  background-color: #45b66f;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px;
  z-index: -1;
  transition: .3s;
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-transform: scale(0);
          transform: scale(0);
}
.btn-custom:hover::before,
.btn-custom:focus::before{
  -webkit-transform: scale(1);
          transform: scale(1);
}
.btn-custom:hover,
.btn-custom:focus{
  color: #fff;
  outline: none;
}
.btn-custom.primary{
  background-color: #479A66;
}
.btn-custom.secondary{
  background-color: #FCCC4C;
  box-shadow: 0 3px 24px rgba(252,204,76,.5);
}
.btn-custom.secondary::before{
  background-color: #e8b93f;
}
.btn-custom.light{
  background-color: transparent;
  box-shadow: none;
  color: #1D2228;
  border: 1px solid rgba(0,0,0,.1);
}
.btn-custom.light::before{
  background-color: #fff;
}
.input-group-append .btn-custom,
.input-group-append .btn-custom::before{
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.btn-custom i,
.btn-link i{
	margin-left: 10px;
  transition: .3s;
}
.btn-custom:hover i,
.btn-link:hover i{
  -webkit-transform: translateX(3px);
          transform: translateX(3px);
}
.btn.icon-behind i,
.btn-link.icon-behind i{
	margin-right: 10px;
}
.btn-link{
  font-weight: 600;
  color: #1D2228;
}
.btn-link:hover,
.btn-link:focus{
  color: #da464a;
  text-decoration: none;
}

.btn-custom.btn-sm{
  padding: 8px 20px;
  font-size: 12px;
}
