/*======================
2. Header
========================*/

.main-header {
  position: relative;
  width: 100%;
  background-color: #f9f9f9;
  z-index: 990;
  transition: background-color 0.3s;
  box-shadow: 0px 6px 6px rgb(0 0 0 / 25%);
}

.main-header.header-absolute {
  position: absolute;
}

.navbar {
  padding: 0;
}

.main-header.can-sticky {
  position: fixed;
  top: 0;
  left: 0;
}
.main-header.sticky{
