}

.aside-toggler {
  display: none;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
  margin: 20px 0;
  margin-left: 15px;
}

.aside-toggler span {
  display: block;
  margin-bottom: 6px;
  width: 25px;
  height: 1px;
  border-radius: 3px;
  background-color: #1D2228;
  transition: .3s;
}
.aside-toggler span:first-child,
